<template>
  <v-app>
    <!-- <HelloWorld /> -->
    <v-container class="d-flex flex-column pb-1">
      <v-alert
        type="error"
        v-show="info.alert_url != ''"
        class="text-caption font-weight-medium mb-1"
        dense
        tile
        outlined
      >
        {{
          `${info.exclude_url.join(", ")} are not working.
          Please use recommended Sci-Hub URL`
        }}
      </v-alert>
      <v-alert
        type="info"
        v-show="info.alert_url != '' || displayRecommendURL"
        class="text-caption font-weight-medium mb-1"
        dense
        tile
        outlined
      >
        {{
          `Recommended Sci-Hub URL: ${
            info.recommend_url.length == 0
              ? "Loading..."
              : info.recommend_url.join(", ")
          }`
        }}
      </v-alert>
      <v-alert
        type="warning"
        v-show="info.info != ''"
        color="#e5940d"
        class="text-caption font-weight-medium mb-1"
        dense
        tile
        outlined
        dismissible
      >
        {{ `${info.info}` }}
      </v-alert>
      <v-select
        v-model="options.selectedURL"
        :items="scihubURL"
        label="Set Sci-Hub URL"
        hide-details
      ></v-select>
      <p
        class="hover primary--text font-weight-medium py-1 mb-0"
        @click="displayRecommendURL = true"
      >
        Recommended Sci-Hub URL
      </p>
      <div v-if="optionExpand">
        <v-row id="jcr">
          <v-col cols="8" class="pb-0">
            <span class="primary--text text-subtitle-1">
              Display JCR Quartile</span
            >
          </v-col>
          <v-col cols="4" class="pb-0">
            <v-switch
              class="pa-0 ma-0"
              v-model="jcr"
              color="primary"
              hide-details
            ></v-switch>
          </v-col>
        </v-row>
        <v-row id="cas">
          <v-col cols="8" class="pb-0">
            <span class="primary--text text-subtitle-1">
              Display CAS Block</span
            >
          </v-col>
          <v-col cols="4" class="pb-0">
            <v-switch
              class="pa-0 ma-0"
              v-model="cas"
              color="primary"
              hide-details
            ></v-switch>
          </v-col>
        </v-row>
        <v-row id="filter">
          <v-col cols="8" class="pb-0">
            <span class="primary--text text-subtitle-1">
              Enable PubMed Filter</span
            >
          </v-col>
          <v-col cols="4" class="pb-0">
            <v-switch
              class="pa-0 ma-0"
              v-model="options.filter"
              color="primary"
              hide-details
            ></v-switch>
          </v-col>
        </v-row>
        <v-row id="abstractFontSize">
          <v-col cols="8" class="pb-0">
            <span class="primary--text text-subtitle-1">
              Abstract Font Size</span
            >
          </v-col>
          <v-col cols="4" class="pb-0 pt-1">
            <v-select
              class="pa-0 ma-0"
              v-model="options.abstractFontSize"
              :items="abstractFontSizes"
              single-line
              hide-details
              append-icon=""
            ></v-select>
          </v-col>
        </v-row>
        <v-row id="translate">
          <v-col cols="8" class="pb-0">
            <span class="primary--text text-subtitle-1">
              Enable Translation</span
            >
          </v-col>
          <v-col cols="4" class="pb-0">
            <v-switch
              class="pa-0 ma-0"
              v-model="options.translate"
              color="primary"
              hide-details
            ></v-switch>
          </v-col>
        </v-row>
        <v-row id="titleTranslate" v-if="options.translate">
          <v-col cols="8" class="pl-6 pb-0 pt-3">
            <span class="primary--text text-body-1"> Translation Button</span>
          </v-col>
          <v-col cols="4" class="pb-0 pt-3">
            <v-switch
              class="pa-0 ma-0"
              v-model="options.titleTranslate"
              color="primary"
              hide-details
            ></v-switch>
          </v-col>
        </v-row>
        <v-row id="translateKey" v-if="options.translate">
          <v-col cols="8" class="pl-6 py-0 d-flex align-end">
            <span class="primary--text text-body-1"> Translate Key</span>
          </v-col>
          <v-col cols="2" class="py-0 pb-0">
            <v-text-field
              v-model="translateKey"
              single-line
              hide-details
              readonly
              class="mt-0 pt-0"
              @keyup="translateKeyUp"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row id="clearKey" v-if="options.translate">
          <v-col cols="8" class="pl-6 py-0 d-flex align-end">
            <span class="primary--text text-body-1"> Clear Key</span>
          </v-col>
          <v-col cols="2" class="py-0 pb-0">
            <v-text-field
              v-model="clearKey"
              single-line
              hide-details
              readonly
              class="mt-0 pt-0"
              @keyup="clearKeyUp"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row id="targetLang" v-if="options.translate" class="pt-1"
          ><v-col cols="11" class="pl-6 py-0">
            <v-select
              v-model="options.targetLang"
              :items="targetLangs"
              label="Target Language"
              hide-details
            ></v-select></v-col
        ></v-row>
        <v-row id="titleTranslate">
          <v-col cols="8" class="pb-0">
            <span
              class="primary--text text-caption"
              style="cursor: pointer"
              title="More information on user experience improvement program"
              @click="privacyPolicyDisplay = true"
            >
              Help improve experience
              <v-icon class="primary--text right small"
                >mdi-dots-horizontal</v-icon
              ></span
            >
          </v-col>
          <v-col cols="4" class="pb-0 pt-3">
            <v-switch
              class="pa-0 ma-0"
              v-model="options.dataCollection"
              color="primary"
              hide-details
            ></v-switch>
          </v-col>
        </v-row>
        <p
          class="primary--text text-subtitle-1 py-1 mt-2 mb-0 hover"
          @click="reset"
        >
          Reset to Default
        </p>
      </div>
      <div>
        <v-col
          cols="12"
          class="d-flex justify-end hover py-1"
          @click="optionExpand = !optionExpand"
        >
          <span v-if="!optionExpand">More Options...</span
          ><v-icon x-small>{{
            optionExpand == true
              ? "mdi-arrow-up-drop-circle"
              : "mdi-arrow-down-drop-circle"
          }}</v-icon>
        </v-col>
      </div>
      <!-- <v-icon color="primary">mdi-refresh</v-icon>&nbsp; -->

      <v-divider />
      <p
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="link(pubmed_filter_url)"
      >
        <v-icon color="primary">mdi-filter</v-icon>&nbsp;PubMed Filter Manager
      </p>
      <p
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="link(reference_manager_url)"
      >
        <v-icon color="primary">mdi-alpha-r-box</v-icon>&nbsp;Reference Manager
      </p>
      <!-- <p
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="link(paper_markdown_url)"
      >
        <v-icon color="primary">mdi-alpha-p-box</v-icon>&nbsp;Paper Markdown
      </p> -->
      <p
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="link('https://pubmed.ncbi.nlm.nih.gov/')"
      >
        <v-icon color="primary">mdi-alpha-p-box</v-icon>&nbsp;Go to PubMed
      </p>
      <p
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="link(options.selectedURL)"
      >
        <v-icon color="primary">mdi-alpha-s-box</v-icon>
        &nbsp;Go to Sci-Hub
      </p>
      <p
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="link('https://translate.google.cn/')"
      >
        <v-icon color="primary">mdi-google-translate</v-icon>
        &nbsp;Google Translation
      </p>
      <div id="searchJournalInfo">
        <v-col cols="12" class="pt-0 pb-1 px-0">
          <v-autocomplete
            class="pt-0"
            v-model="model"
            filled
            :items="pubmed"
            item-text="description"
            item-value="abb"
            label="Journal Information  (IF/JCR-Q/CAS—B)"
            placeholder="Input Journal Name/Abb/ISSN"
            hide-no-data
            hide-details
            return-object
            clearable
            allow-overflow
            spellcheck="false"
          >
          </v-autocomplete>
          <v-expand-transition>
            <div>
              <div v-if="model" class="pb-0 pt-2">
                <p v-for="(value, name, i) in fields" :key="i" class="mb-1">
                  <span
                    style="display: inline-block; font-size: 12px"
                    class="primary--text text-center font-weight-black"
                    >{{ name + " :" }}</span
                  >
                  <!-- background-color: #03a696; -->
                  <span
                    class="pl-2 font-weight-medium text-left"
                    style="font-size: 12px"
                  >
                    {{ value }}</span
                  >
                </p>
                <v-divider />
              </div>
            </div>
          </v-expand-transition>
        </v-col>
      </div>
      <div id="dictionary">
        <v-col cols="12" class="pt-0 pb-1 px-0">
          <v-text-field
            v-model="dictionaryInput"
            clearable
            filled
            label="Dictionary (English to Chinese Simplified)"
            type="text"
            placeholder="Input word"
            spellcheck="false"
            hide-details
            append-icon="mdi-database-search"
            @click:append="dictionarySubmit"
            @keyup.enter="dictionarySubmit"
          >
          </v-text-field>
        </v-col>
        <p
          class="
            pl-3
            pb-0
            mb-1
            text-body-2
            font-weight-bold
            primary--text
            text-center
          "
          v-if="dictionaryDisplay && dictionaryLoading"
        >
          Loading...
        </p>
        <v-col
          id="dictionary-content"
          v-if="dictionaryDisplay && !dictionaryLoading"
          cols="12"
          class="py-0"
        >
          <p
            class="mb-1 font-weight-bold text-h6"
            style="word-wrap: break-word"
          >
            {{ searchWord }}
            <img
              :src="iconURL"
              style="
                width: 1.1em;
                height: 1.1em;
                vertical-align: middle;
                cursor: pointer;
              "
              @click="link('https://cn.bing.com/dict/search?q=' + searchWord)"
            />
          </p>

          <p v-for="(item, i) in dictionaryItems" :key="i" class="mb-1">
            <span
              style="width: 40px; display: inline-block"
              class="primary white--text text-center"
              v-text="item[0]"
            ></span>
            <!-- background-color: #03a696; -->
            <span class="pl-2" v-text="item[1]"></span>
          </p>
        </v-col>
        <v-col
          cols="12"
          class="d-flex justify-end hover py-1"
          @click="dictionaryDisplay = false"
          v-if="dictionaryDisplay"
        >
          <v-icon x-small> mdi-arrow-up-drop-circle </v-icon>
        </v-col>
      </div>
      <div id="searchSciHub">
        <v-col cols="12" class="pt-0 px-0">
          <v-text-field
            v-model="scihubInput"
            filled
            clearable
            label="Search Sci-Hub"
            type="text"
            placeholder="Input PMID/DOI/Title..."
            hide-details
            append-icon="mdi-database-search"
            @click:append="scihubSubmit"
            @keyup.enter="scihubSubmit"
          >
          </v-text-field>
        </v-col>
      </div>
      <v-divider />
      <p
        id="user-manual"
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="link('https://github.com/naivenaive/EasyPubMed#easypubmed')"
      >
        <v-icon color="primary">mdi-book-open</v-icon>&nbsp;User Manual
      </p>
      <p
        id="bug-report-suggestion"
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="
          link(
            'https://github.com/naivenaive/EasyPubMed#%E9%94%99%E8%AF%AF%E6%8A%A5%E5%91%8A%E5%8F%8A%E5%BB%BA%E8%AE%AE'
          )
        "
      >
        <v-icon color="primary">mdi-bug</v-icon>&nbsp;Bug Report &amp;
        Suggestions
      </p>
      <p
        id="donation"
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="
          link('https://github.com/naivenaive/EasyPubMed#%E6%8D%90%E8%B5%A0')
        "
      >
        <v-icon color="primary">mdi-heart-multiple</v-icon>&nbsp;Donation
      </p>
      <p
        id="easypubmed-author"
        class="primary--text text-subtitle-1 my-0 py-1 hover"
        @click="
          link(
            'https://github.com/naivenaive/EasyPubMed#%E5%BC%80%E5%8F%91%E6%95%85%E4%BA%8B'
          )
        "
      >
        <v-icon color="primary">mdi-account-tie</v-icon>&nbsp;More about
        EasyPubMed &amp; Author
      </p>
      <v-divider />
      <p class="primary--text text-subtitle-1 my-0 pt-1 pb-0 text-center">
        {{ `Version ${version} By Tian Zhen` }}
      </p>
      <!-- <v-alert type="success" :value="true" class="mb-0" dense>
        Latest version release: 0.1.1
      </v-alert> -->
      <v-alert
        v-if="info.latest_version != ''"
        type="success"
        dark
        dense
        tile
        class="mb-1"
        style="cursor: pointer"
        @click="link(info.latest_version_url)"
        >{{ `New Version Release: ${info.latest_version}` }}
      </v-alert>
    </v-container>
    <v-dialog
      v-model="privacyPolicyDisplay"
      v-if="privacyPolicyDisplay"
      max-width="400"
    >
      <v-card>
        <v-card-title class="primary white--text py-2 px-3">
          <!-- <span>Data Collection Policy</span> -->
          <span class="text-caption font-weight-bold"
            >User Experience Improvement Program</span
          >
        </v-card-title>
        <v-card-text
          class="px-3 pb-1"
          style="max-height: 450px; overflow-y: auto"
        >
          <p class="grey--text caption mb-0 py-2" style="line-height: 17px">
            We may collect the information of some websites you visit. The
            collected data will be used for further feature development which is
            aimed to improve user experience such as obtaining full-text PDF.
            All data collected is sent anonymously and encrypted. No personal
            information that can identify you is collected, shared, submitted or
            disclosed with any other party.
            <b
              >You can quit user experience improvement program by disable the
              'Help improve experience' switch in the popup page.</b
            >
            More information about privacy policy can be found
            <span
              class="text-decoration-underline"
              style="cursor: pointer"
              @click="
                link(
                  'https://github.com/naivenaive/EasyPubMed/blob/master/EasyPubMed%20User%20Manual.md#donation'
                )
              "
              >here</span
            >.
          </p>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="snackbar" color="error" top timeout="5000">
      {{ snackbarText }}
      <template v-slot:action="{ attrs }">
        <v-icon
          class="mr-3"
          small
          color="white"
          v-bind="attrs"
          @click="snackbar = false"
          >mdi-close</v-icon
        >
      </template>
    </v-snackbar>
    <form
      ref="scihubForm"
      :action="options.selectedURL"
      method="POST"
      target="_blank"
      style="display: none"
    >
      <input type="hidden" name="request" :value="scihubInput" />
    </form>
  </v-app>
</template>

