      abstractFontSizes: [
        { value: "1.4rem", text: "Normal" },
        { value: "1.5rem", text: "Large" },
        { value: "1.6rem", text: "X-Large" },
      ],
    };
  },
  computed: {
    pubmed() {
      return pubmed_abb.map(function (i) {
        i.description = `${i.pubmed_journal} (${i.abb})`;
        if (i.pubmed_issn != "NA")
          i.description += `  [ISSN: ${i.pubmed_issn}]`;
        if (i.pubmed_eissn != "NA")
          i.description += `  [eISSN: ${i.pubmed_eissn}]`;
        return i;
      });
    },
    fields() {
      var field = {};
      field["Journal"] = this.model["pubmed_journal"];
      field["Journal Abbreviation"] = this.model["abb"];
      field["ISSN"] = this.model["pubmed_issn"];
      field["eISSN"] = this.model["pubmed_eissn"];

      var filter_abbs = filter_data.filter((i) => i.abb === this.model.abb);
      console.log("filter_abbs", filter_abbs);
      var filter_abb = filter_abbs[0];
      if (filter_abbs.length > 0) {
        field["SCIE Indexed"] = "True";
        field["Impact Factor"] = filter_abb["IF"];
        field["JCR Quartile"] = filter_abb["Q"];
        field["CAS Block"] = filter_abb["B"];
        field["Top/Non-Top Journal"] =
          filter_abb["T"] == 1 ? "Top Journal" : "Non-Top Journal";
        let areaCategoryArray = [];
        let areaCategoryArrayCn = [];
        for (var i of filter_abbs) {
          areaCategoryArray.push(`${i.class}/${i.subclass}`);
          areaCategoryArrayCn.push(`${i.class_cn}/${i.subclass_cn}`);
        }
        field["Subject Area/Category"] = areaCategoryArray.join(" ; ");
        field["杂志分类（大类/小类）"] = areaCategoryArrayCn.join(" ; ");
      } else {
        field["SCIE Indexed"] = "False";
      }
      console.log(field);
      return field;
    },
    version() {
      var manifestData = chrome.runtime.getManifest();
      return manifestData.version;
    },
    pubmed_filter_url() {
      return chrome.extension.getURL("pubmedFilter.html");
    },
    reference_manager_url() {
      return chrome.extension.getURL("referenceManager.html");
    },
    // paper_markdown_url() {
    //   return chrome.extension.getURL("paperMarkdown.html");
    // },
  },
  methods: {
    link(url) {
      // console.log("url", url);
      window.open(url);
    },
    reset() {
      // var _this = this;
      for (var i in this.default_options) {
        this.options[i] = this.default_options[i];
      }
      // this.options = this.default_options;
      this.jcr = this.default_options.jcr;
      this.cas = this.default_options.cas;
      this.translateKey = this.default_options.translateKey.toUpperCase();
      this.clearKey = this.default_options.clearKey.toUpperCase();
      // chrome.storage.local.set({ options: this.default_options }, function () {
      chrome.storage.local.set({ options: this.options }, function () {
        // console.log("Defalut Options ", _this.options);
      });
    },
    dictionarySubmit() {
      if (this.dictionaryInput == "") {
        window.open("https://cn.bing.com/dict/");
        return;
      }
      var _this = this;
      this.searchWord = this.dictionaryInput;
      _this.dictionaryDisplay = true;
      _this.dictionaryLoading = true;

      chrome.runtime.sendMessage(
        {
          command: "dictionary_bing",
          text: this.dictionaryInput,
          target_lang: "zh-cn",
        },
        function (response) {
          console.log(response);

          if (response.bing_text == "Error") {
            _this.dictionaryItems = [["结果", "未查到"]];
            _this.dictionaryLoading = false;
            return;
          }
          _this.dictionaryItems = response.bing_array;
          _this.dictionaryLoading = false;
        }
      );
    },
    scihubSubmit() {
      if (this.scihubInput == "") {
        window.open(this.options.selectedURL);
      } else {
        this.$refs.scihubForm.submit();
      }
    },
    translateKeyUp(event) {
      //   console.log(event);
      if (
        event.key.toLowerCase().match(/^[a-z]{1}$/) != null &&
        event.key.toLowerCase() != this.options.clearKey
      ) {
        this.translateKey = event.key.toUpperCase();
        this.options.translateKey = event.key.toLowerCase();
      } else {
        this.translateKey = this.options.translateKey.toUpperCase();
        this.snackbarText =
          "Valid key should be A-Z. Translate key should NOT be the same as Clear key";
        this.snackbar = true;
      }
    },
    clearKeyUp(event) {
      //   console.log(event);
      if (
        event.key.toLowerCase().match(/^[a-z]{1}$/) != null &&
        event.key.toLowerCase() != this.options.translateKey
      ) {
        this.clearKey = event.key.toUpperCase();
        this.options.clearKey = event.key.toLowerCase();
      } else {
        this.clearKey = this.options.clearKey.toUpperCase();
        this.snackbarText =
          "Valid key should be A-Z. Translate key should NOT be the same as Clear key";
        this.snackbar = true;
      }
    },
  },
  watch: {
    jcr(n) {
      this.cas = !n;
      this.options.cas = this.cas;
    },
    cas(n) {
      this.jcr = !n;
      this.options.jcr = this.jcr;
    },
    options: {
      deep: true,
      handler(n, o) {
        // console.log("n", n, "o", o);
        chrome.storage.local.set({ options: n }, function () {
          // console.log("Options is set to ", n);
        });
        if (this.info.exclude_url.includes(n.selectedURL)) {
          this.info.alert_url = n.selectedURL;
        }
      },
    },
  },
  created() {
    var options_get;
    var _this = this;
    // console.log("ini", this.options);
    var get_options = new Promise((resolve) => {
      chrome.storage.local.get("options", function (result) {
        // console.log(result, "ree");
        options_get = result.options;
        _this.options = options_get;
        _this.jcr = options_get.jcr;
        _this.cas = options_get.cas;
        _this.translateKey = options_get.translateKey.toUpperCase();
        _this.clearKey = options_get.clearKey.toUpperCase();
        resolve(options_get);
      });
    });
    get_options.then(
      chrome.runtime.sendMessage({ command: "get_info" }, function (response) {
        console.log("info", response);
        _this.info.recommend_url = response.recommend_url;
        _this.info.exclude_url = response.exclude_url;

        // console.log(compare_version(response.latest_version, _this.version));
        if (compare_version(response.latest_version, _this.version) == 1) {
          //for old version
          _this.info.latest_version = response.latest_version;
          _this.info.latest_version_url = response.latest_version_url;
          _this.info.info = response.info_old_version;
        } else {
          //for latest version
          _this.info.info = response.info;
        }
        if (response.exclude_url.includes(_this.options.selectedURL)) {
          _this.info.alert_url = _this.options.selectedURL;
        }
      })
    );
  },
};

function compare_version(latest_version, version) {
  // console.log(latest_version, version);
  let latest_version_array = latest_version.split(".");
  let version_array = version.split(".");
  for (var i in latest_version) {
    if (parseInt(latest_version_array[i]) > parseInt(version_array[i])) {
      return 1;
    }
    if (parseInt(latest_version_array[i]) < parseInt(version_array[i])) {
      return -1;
    }
  }
  return 0;
}
</script>

